import React from 'react';
import styles from './app-header.module.css';
import {
    Logo
} from '@ya.praktikum/react-developer-burger-ui-components';
import HeaderLink from "../header-link/header-link";
import {Link} from "react-router-dom";
import {useAppSelector} from "../../services/store";

export default function AppHeader(): JSX.Element {
    const user = useAppSelector((store) => store.auth);

    return (
        <header className={styles.header}>
            <nav className={`${styles.body} p-4`}>
                <section>
                    <HeaderLink icon={'BurgerIcon'} path={'/'}></HeaderLink>
                    <HeaderLink icon={'ListIcon'} path={'/feed'}> </HeaderLink>
                </section>

                <section className={styles.logo}>
                    <Link to="/">
                        <Logo />
                    </Link>
                </section>

                <section>
                    {user.isAuthorized ? (
                        <HeaderLink icon={'ProfileIcon'} path={'/profile'}>{` `}</HeaderLink>
                    ) : (
                        <HeaderLink icon={'ProfileIcon'} path={'/login'}></HeaderLink>
                    )}
                </section>
            </nav>
        </header>
    );
};